<template>
  <VanPopup
    class="components-payTypeDialog"
    :value="value"
    @input="val => this.$emit('input', val)"
    round
    position="bottom"
    closeable
    close-icon="close"
    :style="{ height:'30%' }">
    <div class="title">请选择支付方式</div>
    <div class="icon-box">
      <img
        @click="select(item.key)"
        :src="item.img"
        :class="['img-box', { 'ml__130': index }]"
        v-for="(item, index) in iconList"
        :key="index" />
    </div>
  </VanPopup>
</template>
<script>
export default {
  name: 'PayTypeDialog',
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      iconList: [
        // 图标列表
        {
          img: require('@/assets/baiduAB/aliPay.png'),
          key: '1'
        },
        {
          img: require('@/assets/baiduAB/wxPay.png'),
          key: '2'
        }
      ]
    }
  },
  methods: {
    select (key) {
      this.$emit('change', key)
      this.$emit('input', false)
    }
  }
}
</script>
